$color: var(--black-accent-color2-rgb);

#img-background-violet {
  position: absolute;
  width: 100%;
  height: 100vh;
  z-index: -10;
  background-image: url("../../../assets/img/lamborghini-home.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  object-fit: cover;

  &::after {
    content: "";
    position: absolute;
    bottom: 0px;
    left: 0px;
    width: 100%;
    height: 100vh;
    background: linear-gradient(
      to right,
      rgba($color, 0.8),
      transparent,
      transparent,
      transparent
    );
  }
}

@media (max-width: 1024px) {
  #img-background-violet {
    background-image: url("../../../assets/img/lamborghini-home-mobile.jpg");
    &::after {
      content: "";
      height: 100vh;
      background: linear-gradient(
        to top,
        rgba($color, 1),
        rgba($color, 0.25),
        transparent,
        transparent
      );
    }
  }
}
